import {useEffect, useState} from "react";
import * as React from 'react';
import {Loader} from "../../components/loader/loader";
export const LiveMakeUp =()=>{
const [loading , setLoading]= useState(true);

  useEffect(()=>{
    const start = () => {
      const success = window.VmtHelper.registerCanvases(
        "tryon-live-canvas-0",
        "tryon-live-canvas-1"
      );
      if (success) {
        window.VmtHelper.setLive(true);
        window.CameraController.streamToCanvasId("tryon-live-canvas-0");
        window.CameraController.onEachFrame(() => {
          window.VmtHelper.cycleForLive();
        });
        window.CameraController.start();
        setLoading(false)
      }
    }

    document.addEventListener("vmt_is_ready",start );
    return ()=>{ document.removeEventListener("vmt_is_ready",start );}
  },[])

  return <div>{  <Loader  showIt={loading}/>} </div>
}